<!-- 个人信息页面 -->
<template>
    <div class="box">
        <el-form :label-position="labelPosition" label-width="80px" :model="personalForm">
            <el-form-item label="用户名">
                <el-input v-model="personalForm.userName"></el-input>
            </el-form-item>
            <el-form-item label="所属公司">
                <el-input v-model="personalForm.userCompany"></el-input>
            </el-form-item>
            <el-form-item label="密码">
                <el-input v-model="personalForm.password"></el-input>
            </el-form-item>
            <el-form-item label="电话号码">
                <el-input v-model="personalForm.userPhone"></el-input>
            </el-form-item>
            <el-radio-group v-model="sex" class="radio">
                <el-radio :label="'男'">男</el-radio>
                <el-radio :label="'女'">女</el-radio>
            </el-radio-group>
            <el-button type="primary" class="sure" @click="updateInfo">确定</el-button>
            <div>上次修改时间：{{ updateTime }}</div>
        </el-form>
    </div>
</template>

<script>
export default {
    name: 'personalInfo',
    data() {
        //验证电话号码
        var validatePhone = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入电话号码'));
            } else {
                callback()
            }
        };
        //验证用户名
        var validateName = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入用户名'));
            } else {
                callback()
            }
        }
        //验证所属公司
        var validateCompany = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入所属公司'));
            } else {
                callback()
            }
        }
        //验证密码
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入密码'));
            } else {
                if (this.ruleForm.checkPass !== '') {
                    this.$refs.ruleForm.validateField('checkPass');
                }
                callback();
            }
        };
        return {
            labelPosition: 'right',
            personalForm: {
                password: "",
                userPhone: "",
                userName: "",
                userCompany: ''
            },
            rules: {
                password: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                userPhone: [
                    { validator: validatePhone, trigger: 'blur' }
                ],
                userName: [
                    { validator: validateName, trigger: 'blur' }
                ],
                userCompany: [
                    { validator: validateCompany, trigger: 'blur' }
                ]
            },
            sex: '男'
        };
    },
    methods: {
        updateInfo() {
            const { userName, userCompany, password, userPhone} = this.personalForm
            const id=localStorage.getItem('id')
            const {sex}=this
            const form = { id, userName, userCompany, password, userPhone, sex }
            this.$store.dispatch("updateInfo", {form,vm:this})
        }
    },
    mounted() {
        this.$store.dispatch("getUserInfo", localStorage.getItem('id'))
    }
}
</script>

<style lang="scss" scoped> .box {
     width: 50%;
     height: 600px;
     float: left;
     margin-left: 200px;
     margin-top: 100px;

     .el-form-item {
         margin-bottom: 50px;
     }

     .radio {
         transform: scale(1.5) translate(200px, -50px);
     }

     .sure {
         margin-left: 210px;
         margin-top: 50px;
     }
 }
</style>